import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import componentStyles from "assets/theme/components/admin-navbar.js";
import "assets/css/Staking.scss"
import axios from "axios"
import { DataGrid } from '@material-ui/data-grid';
import { WalletMultiButton } from '@solana/wallet-adapter-react-ui';
import Input from '@material-ui/core/Input';
import Button from "@material-ui/core/Button";
import idl from './idls/idl.json'
import poolSecret from './idls/pool.json'
import { PublicKey } from '@solana/web3.js'
import { Program, Provider, BN, web3 } from '@project-serum/anchor'
import { TOKEN_PROGRAM_ID, Token } from "@solana/spl-token"
import {
    useWallet,
    useConnection
} from '@solana/wallet-adapter-react'

require('@solana/wallet-adapter-react-ui/styles.css')
const { SystemProgram, Keypair, SYSVAR_CLOCK_PUBKEY } = web3
const programID = new PublicKey("2TdCUSBu589CUqvdaWpQkNByYes4rREMU7DydnPfJWQA")
const poolKey = new PublicKey(new Uint8Array(poolSecret))
const opts = {
    preflightCommitment: "processed"
}


const columns = [
    {
        field: 'account',
        headerName: 'account',
        width: 400,
    },
    {
        field: 'staked_amount_14',
        headerName: 'Staked Amount(14D)',
        width: 150,
    },

    {
        field: 'staked_amount_30',
        headerName: 'Staked Amount(30D)',
        width: 150,
    },

    {
        field: 'staked_amount_90',
        headerName: 'Staked Amount(90D)',
        width: 150,
    },

    {
        field: 'staked_time_14',
        headerName: 'Staked Time(14D)',
        width: 200,

    },

    {
        field: 'staked_time_30',
        headerName: 'Staked Time(30D)',
        width: 200,

    },

    {
        field: 'staked_time_90',
        headerName: 'Staked Time(90D)',
        width: 200,

    },

    {
        field: 'reward_14',
        headerName: 'Reward Amount(14D)',
        width: 110,

    },

    {
        field: 'reward_30',
        headerName: 'Reward Amount(30D)',
        width: 110,

    },

    {
        field: 'reward_90',
        headerName: 'Reward Amount(90D)',
        width: 110,

    },

];

const useStyles = makeStyles(componentStyles);

export default function Staking() {
    const wallet = useWallet();
    const { connection } = useConnection();
    const [balance, setBalance] = useState(0);
    const [userCnt, setUserCnt] = useState(0);
    const [rewardAmount, setRewardAmount] = useState(0);
    const [stakedData, setStakedData] = useState([]);
    const [depositAmt, setDepositAmt] = useState(0);
    const [withdrawAmt, setWithdrawAmt] = useState(0);
    const getProvider = async () => {
        const provider = new Provider(
            connection, wallet, opts.preflightCommitment
        )
        return provider;
    }
    const getWalletBalance = async (pubkey) => {
        console.log(wallet, pubkey, "how")
        if (!wallet.publicKey) {
            return 0
        }
        console.log(wallet, "why")
        const provider = await getProvider()
        const tokens = await provider.connection.getTokenAccountsByOwner(wallet.publicKey, { mint: pubkey })
        if (tokens.value.length === 0) {
            return 0
        }
        console.log(tokens, "tok")
        const token = tokens.value.pop()
        const val = (await provider.connection.getTokenAccountBalance(token.pubkey)).value
        console.log(val, "val")
        setBalance(val.uiAmount)
    }


    const getAllData = async () => {
        console.log("ss");
        const url = 'http://ec2-44-202-249-16.compute-1.amazonaws.com:3333/staking/init';
        const accData = await axios.get(url);
        console.log(accData, "userData");
        const provider = await getProvider();
        const program = new Program(idl, programID, provider);
        const poolObject = await program.account.pool.fetch(poolKey)
        setRewardAmount(poolObject.adminRewardAmount.toNumber() / (10 ** 9))
        if (accData.data.data) {
            setUserCnt(accData.data.data.length);
            const userData = [];
            for (let i = 0; i < accData.data.data.length; i++) {
                try {
                    const data = accData.data.data[i];
                    const userOne = {};
                    const [_userPubkey, _userNonce] = await PublicKey.findProgramAddress(
                        [(new PublicKey(accData.data.data[i].account)).toBuffer(), poolKey.toBuffer()],
                        programID
                    );
                    const Adata = await program.account.user.fetch(_userPubkey)
                    userOne.id = i;
                    userOne.account = data.account
                    userOne.staked_amount_14 = Adata.balanceStakedA.toNumber() / (10 ** 9)
                    userOne.staked_amount_30 = Adata.balanceStakedB.toNumber() / (10 ** 9)
                    userOne.staked_amount_90 = Adata.balanceStakedC.toNumber() / (10 ** 9)
                    userOne.staked_time_14 = (new Date(Adata.stakeTimeA.toNumber() * 1000)).toISOString()
                    userOne.staked_time_30 = (new Date(Adata.stakeTimeB.toNumber() * 1000)).toISOString()
                    userOne.staked_time_90 = (new Date(Adata.stakeTimeC.toNumber() * 1000)).toISOString()
                    if ((((new Date().getTime()) / 1000) - (Adata.stakeTimeA.toNumber())) > 20 * 60) {
                        userOne.reward_14 = (((10 * Adata.balanceStakedA.toNumber() / 365 / 24 / 60 / 60 / 100) / (10 ** 9)) * (((new Date().getTime()) / 1000) - (Adata.lastUpdateTimeB.toNumber()))).toFixed(2)
                    }
                    if ((((new Date().getTime()) / 1000) - (Adata.stakeTimeB.toNumber())) > 25 * 60) {
                        userOne.reward_30 = (((20 * Adata.balanceStakedB.toNumber() / 365 / 24 / 60 / 60 / 100) / (10 ** 9)) * (((new Date().getTime()) / 1000) - (Adata.lastUpdateTimeB.toNumber()))).toFixed(2)
                    }
                    if ((((new Date().getTime()) / 1000) - (Adata.stakeTimeC.toNumber())) > 30 * 60) {
                        userOne.reward_90 = (((10 * Adata.balanceStakedC.toNumber() / 365 / 24 / 60 / 60 / 100) / (10 ** 9)) * (((new Date().getTime()) / 1000) - (Adata.lastUpdateTimeC.toNumber()))).toFixed(2)
                    }
                    userData.push(userOne)
                } catch (e) {
                    console.log(e)
                }
            }
            console.log(userData, "userData")
            setStakedData(userData)
        }
    }

    const fund = async (amount) => {
        if (amount == 0) {
            alert("Balance is 0!")
            return
        }
        if (amount > balance) {
            alert("This is more than your balance!")
            return
        }
        const provider = await getProvider()
        const program = new Program(idl, programID, provider)
        const pubkeyToUse = poolKey
        const poolObject = await program.account.pool.fetch(pubkeyToUse)

        const stakingMintObject = new Token(
            provider.connection,
            new PublicKey('8W8T4w8zauZxC32FShCQVcbkrWinnXFNr8ohFbZ79jZN'),
            TOKEN_PROGRAM_ID,
            provider.wallet.payer)
        console.log(wallet, "objec")
        const stakingAccountInfo = await stakingMintObject.getOrCreateAssociatedAccountInfo(wallet.publicKey);
        const stakingPubkey = stakingAccountInfo.address;
        console.log(stakingPubkey, "pu")
        const [
            _poolSigner,
            _nonce
        ] = await PublicKey.findProgramAddress(
            [pubkeyToUse.toBuffer()],
            programID
        )
        const poolSigner = _poolSigner
        try {
            await program.rpc.fundStaking(
                new BN(amount * (10 ** 9)),
                {
                    accounts: {
                        // Stake instance.
                        pool: pubkeyToUse,
                        stakingVault: poolObject.stakingVault,
                        funder: provider.wallet.publicKey,
                        from: stakingPubkey,
                        // Program signers.
                        poolSigner,
                        // Misc.
                        tokenProgram: TOKEN_PROGRAM_ID
                    }
                })
            getWalletBalance(new PublicKey("8W8T4w8zauZxC32FShCQVcbkrWinnXFNr8ohFbZ79jZN"))
            getAllData()
        } catch (err) {
            console.log(err)
        }
    }

    const withdraw = async (amount) => {
        const provider = await getProvider()

        if (amount === 0) {
            alert("Balance is 0!")
            return
        }
        if (amount > rewardAmount) {
            alert("This is more than your balance!")
            return
        }


        const program = new Program(idl, programID, provider)
        const pubkeyToUse = poolKey
        const poolObject = await program.account.pool.fetch(pubkeyToUse)

        const stakingMintObject = new Token(
            provider.connection,
            new PublicKey('8W8T4w8zauZxC32FShCQVcbkrWinnXFNr8ohFbZ79jZN'),
            TOKEN_PROGRAM_ID,
            provider.wallet.payer)
        const stakingAccountInfo = await stakingMintObject.getOrCreateAssociatedAccountInfo(wallet.publicKey)
        const stakingPubkey = stakingAccountInfo.address

        const [
            _poolSigner,
            _nonce
        ] = await PublicKey.findProgramAddress(
            [pubkeyToUse.toBuffer()],
            programID
        )
        const poolSigner = _poolSigner
        try {

            await program.rpc.withdraw(
                new BN(amount * (10 ** 9)),
                {
                    accounts: {
                        // Stake instance.
                        pool: pubkeyToUse,
                        stakingVault: poolObject.stakingVault,
                        funder: provider.wallet.publicKey,
                        from: stakingPubkey,
                        // Program signers.
                        poolSigner,
                        // Misc.
                        tokenProgram: TOKEN_PROGRAM_ID
                    }
                })
            getWalletBalance(new PublicKey("8W8T4w8zauZxC32FShCQVcbkrWinnXFNr8ohFbZ79jZN"))
            getAllData()
        } catch (err) {
            console.log(err)
        }

    }

    useEffect(() => {
        if (wallet.publicKey) {
            getWalletBalance(new PublicKey("8W8T4w8zauZxC32FShCQVcbkrWinnXFNr8ohFbZ79jZN"))
            getAllData()
        }
    }, [wallet.publicKey])

    return (
        <>
            <Grid id="body">
                <Grid id="header">
                    <WalletMultiButton id="walletConnect" />
                </Grid>
                <Grid id="main">
                    <Grid id="title">
                        Staking Management
                    </Grid>
                    <Grid id="dataSection">
                        <Grid id="statusData" container>
                            <Grid item xs={12} xl={4}>SEEDED Balance:&nbsp;{balance}&nbsp;SEEDED</Grid>
                            <Grid item xs={12} xl={4}>User Count:&nbsp;{userCnt}</Grid>
                            <Grid item xs={12} xl={4}>Reward Amounts:&nbsp;{rewardAmount}&nbsp;SEEDED</Grid>
                        </Grid>
                        <Grid id="actionSection" container>
                            <Grid item xs={12} xl={6} id='depositSection'>
                                <Grid item xs={6} xl={4}>
                                    <Input type="number" value={depositAmt} onChange={(e) => setDepositAmt(e.target.value)}></Input>
                                </Grid>
                                <Grid item xs={6} xl={4} id="approveAct">
                                    <Button onClick={() => setDepositAmt(balance)}>Max</Button>
                                    <Button onClick={() => fund(depositAmt)}>Deposit</Button>
                                </Grid>
                            </Grid>
                            <Grid item xs={12} xl={6} id="withdrawSection">
                                <Grid item xs={6} xl={4}>
                                    <Input type="number" value={withdrawAmt} onChange={(e) => setWithdrawAmt(e.target.value)}></Input>
                                </Grid>
                                <Grid item xs={6} xl={4} id="approveAct">
                                    <Button onClick={() => setWithdrawAmt(rewardAmount)}>Max</Button>
                                    <Button onClick={() => withdraw(withdrawAmt)}>Withdraw</Button>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid id="userSection">
                        <Grid id="listTitle">User Lists</Grid>
                        <div id="userList" style={{ height: 400, width: '100%' }}>
                            <DataGrid
                                rows={stakedData}
                                columns={columns}
                                pageSize={5}
                                checkboxSelection
                                disableSelectionOnClick
                            />
                        </div>
                    </Grid>
                </Grid>
            </Grid>
        </>
    );
}
